import React from "react";
import DataGrid from "../../containers/DataGrid";
import ReactHtmlParser from "react-html-parser";

const App = () => {
  const configureCol = [
    {
      property: "name",
      headerText: "Name",
    },
    {
      property: "description",
      headerText: "Description",
      type: "Textarea",
      render: (data) => <> {ReactHtmlParser(data.description)} </>,
    },
  ];
  return (
    <>
      <DataGrid
        objectClass="Store"
        columns={configureCol}
        query={(q) => q}
        fetchData={{
          action: "findStore",
        }}
      />
    </>
  );
};
export default App;
