import React from "react";
import DataGrid from "../../containers/DataGrid";
import ReactHtmlParser from "react-html-parser";
import { Cluster } from "grommet-icons";
import Parse from "../../library/parse";
const App = () => {
  const configureCol = [
    {
      property: "name",
      headerText: "Name",
    },
    {
      property: "description",
      headerText: "Info",
      type: "Textarea",
      render: (data) => <> {ReactHtmlParser(data.description)} </>,
    },
    {
      property: "objectId",
      headerText: "Product",
      primaryKey: true,
      render: (data) => (
        <>
          <Cluster />
        </>
      ),
    },
  ];
  return (
    <>
      <DataGrid
        width="100%"
        objectClass="Store"
        columns={configureCol}
        query={(q) => q}
        fetchData={{
          action: "searchStore",
          params: {
            query: {
              user: {
                f: "equalTo",
                t: "pointer",
                c: "User",
                v: Parse && Parse.User && Parse.User.current().id,
              },
            },
          },
        }}
      />
    </>
  );
};
export default App;
